.songEditor {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 1.2rem; }

.songEditor-source {
  flex: 0 1 50%;
  display: flex;
  overflow-y: scroll; }
  .theme-dark .songEditor-source {
    background-color: #121616; }

.songEditor-preview {
  flex: 0 1 50%;
  padding: 1rem 0;
  overflow-y: scroll; }
  .theme-dark .songEditor-preview {
    background-color: #293132; }
