.sb-optionToggle {
  display: flex;
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem 0.5rem 1rem; }
  .theme-light .sb-optionToggle {
    border-bottom: 1px dotted; }
  .theme-dark .sb-optionToggle {
    border-bottom: 1px dotted #71819e; }
  .theme-dark .sb-optionsGroup .sb-optionToggle {
    background-color: #1b2028; }
  .theme-dark .sb-optionToggle.sb-optionToggle-isNotInteractable {
    color: #71819e; }
  .sb-optionToggle:hover:not(.sb-optionToggle-isNotInteractable) {
    cursor: pointer; }
    .theme-dark .sb-optionToggle:hover:not(.sb-optionToggle-isNotInteractable) {
      background-color: #596883; }

.sb-optionToggle-desc {
  flex: 1 1 auto; }

.sb-optionToggle-icon {
  flex: 0 0 auto; }

.theme-dark .sb-optionToggle-isOff .icon {
  color: #a8a8a8; }

.sb-optionToggle-icon .icon {
  font-size: 1.8rem; }

.theme-dark .sb-optionToggle-isOff .sb-optionToggle-icon .icon {
  color: #a8a8a8; }
