.sb-optionSelect {
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem 0.5rem 1rem; }
  .theme-light .sb-optionSelect {
    border-bottom: 1px dotted; }
  .theme-dark .sb-optionSelect {
    border-bottom: 1px dotted #71819e; }
  .theme-dark .sb-optionsGroup .sb-optionSelect {
    background-color: #1b2028; }
  .theme-dark .sb-optionSelect.sb-optionSelect-isNotInteractable {
    color: #71819e; }
  .sb-optionSelect:hover:not(.sb-optionSelect-isNotInteractable) {
    cursor: pointer; }
    .theme-dark .sb-optionSelect:hover:not(.sb-optionSelect-isNotInteractable) {
      background-color: #596883; }

.sb-optionSelect-title {
  display: flex; }

.sb-optionSelect-desc {
  flex: 1 1 auto; }

.sb-optionSelect-toggle {
  flex: 0 0 auto; }

.sb-optionSelect-icon .icon {
  font-size: 1.8rem; }

.sb-optionSelectChoice {
  padding-top: 0.5rem;
  display: flex; }
  .sb-optionSelectChoice:hover {
    text-decoration: underline;
    cursor: pointer; }

.sb-optionSelectChoice-isSelected {
  font-weight: 700; }

.sb-optionSelectChoice-check {
  flex: 0 0 20px; }

.sb-optionSelectChoice-checkIcon .icon {
  font-size: 1.2rem; }

.sb-optionSelectChoice-label {
  flex: 1 1 auto; }
