.fileManager-isExpanded {
  font-size: 1.2rem; }
  .leftBar-isCollapsed .fileManager-isExpanded {
    display: none; }

.fileManager-isCollapsed {
  padding: 1rem 0; }
  .leftBar:not(.leftBar-isCollapsed) .fileManager-isCollapsed {
    display: none; }

.fileManager-actionsList {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.fileManagerAction {
  flex: 0 1 auto;
  padding: 0.5rem;
  margin-right: 0.5rem;
  white-space: nowrap; }
  .fileManagerAction:hover {
    cursor: pointer; }
    .theme-dark .fileManagerAction:hover {
      background-color: #596883; }

.fileManagerAction-icon .icon {
  font-size: 1.4rem; }

.fileManager-entriesList {
  padding-left: 0;
  list-style: none; }

.fileManagerEntry {
  cursor: pointer; }
  .theme-dark .fileManagerEntry:hover {
    background-color: #596883; }
  .theme-dark .fileManagerEntry.fileManagerEntry-isSelected {
    background-color: #fabf2b; }

.fileManagerEntry-input {
  width: 100%;
  padding: 0.25rem 1rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none; }
  .theme-dark .fileManagerEntry-input {
    color: #f4f4f4; }
  .theme-dark .fileManagerEntry-isSelected .fileManagerEntry-input {
    color: #303846; }
  .fileManagerEntry-isRenamed .fileManagerEntry-input {
    cursor: text; }
    .theme-dark .fileManagerEntry-isRenamed .fileManagerEntry-input {
      background-color: #f4f4f4;
      color: #303846; }
