.mainNav-entries {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  list-style-type: none;
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  vertical-align: middle; }

.mainNavEntry {
  display: inline-block;
  padding: 0 2rem;
  cursor: pointer; }
  .theme-dark .mainNavEntry:hover {
    color: #f4f4f4;
    background-color: #455064; }
  .theme-dark .mainNavEntry.mainNavEntry-isActive {
    color: #f4f4f4;
    background-color: #1b2028; }

.mainNavEntry-icon .icon {
  margin-right: 0.5rem;
  font-size: 1.4rem; }
