.prosemirrorWrapper {
  flex: 1 1 auto;
  overflow-y: scroll;
  padding: 1rem 0; }

.ProseMirror {
  overflow-y: scroll;
  outline: none;
  font-family: "PT Mono", consolas, courier, monospace;
  font-size: 1.2rem; }
  .theme-dark .ProseMirror {
    color: #dbdbdb; }
  .ProseMirror p {
    margin: 0;
    padding: 0 1rem; }
