html, body, #app {
  height: 100%;
  padding: 0; }

.appLayout-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; }

.leftBar {
  flex-shrink: 0;
  width: 15%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; }
  .leftBar.leftBar-isCollapsed {
    width: 48px; }
  .theme-light .leftBar.leftBar-isCollapsed {
    border-right: 1px solid; }
  .theme-dark .leftBar {
    color: #f4f4f4;
    background-color: #303846; }
    .theme-dark .leftBar.leftBar-isCollapsed {
      border-right: 1px solid #dbdbdb; }
      .theme-dark .leftBar.leftBar-isCollapsed:hover {
        background-color: #455064; }
  @media print {
    .leftBar {
      display: none; } }

.rightBar {
  flex-shrink: 0;
  width: 15%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; }
  .rightBar.rightBar-isCollapsed {
    width: 48px; }
  .theme-light .rightBar.rightBar-isCollapsed {
    border-left: 1px solid; }
  .theme-dark .rightBar {
    color: #f4f4f4;
    background-color: #303846; }
    .theme-dark .rightBar.rightBar-isCollapsed {
      border-left: 1px solid #dbdbdb; }
      .theme-dark .rightBar.rightBar-isCollapsed:hover {
        background-color: #455064; }
  @media print {
    .rightBar {
      display: none; } }

.leftBar-content {
  overflow-y: scroll;
  flex: 1 1 auto; }
  .leftBar-isCollapsed .leftBar-content {
    padding: 0.5rem 1rem;
    text-align: center;
    cursor: pointer; }

.rightBar-content {
  overflow-y: scroll;
  flex: 1 1 auto; }
  .rightBar-isCollapsed .rightBar-content {
    padding: 0.5rem 1rem;
    text-align: center;
    cursor: pointer; }

.leftBar-collapser {
  flex: 0 0 12px;
  display: flex;
  cursor: pointer; }
  .leftBar-isCollapsed .leftBar-collapser {
    display: none; }
  .theme-light .leftBar-collapser {
    border-right: 1px solid; }
  .theme-dark .leftBar-collapser {
    background-color: #405a73;
    border-right: 1px solid #a8a8a8; }
    .theme-dark .leftBar-collapser:hover {
      background-color: #537393; }
  .theme-dark .leftBar-collapser {
    color: #f4f4f4; }

.leftBar-collapserIcon {
  flex: 1 1 auto;
  align-self: center;
  font-weight: 700; }
  .leftBar-collapserIcon .icon {
    font-size: 1.2rem; }

.rightBar-collapser {
  flex: 0 0 12px;
  display: flex;
  cursor: pointer; }
  .rightBar-isCollapsed .rightBar-collapser {
    display: none; }
  .theme-light .rightBar-collapser {
    border-left: 1px solid; }
  .theme-dark .rightBar-collapser {
    background-color: #405a73;
    border-left: 1px solid #a8a8a8; }
    .theme-dark .rightBar-collapser:hover {
      background-color: #537393; }
  .theme-dark .rightBar-collapser {
    color: #f4f4f4; }

.rightBar-collapserIcon {
  flex: 1 1 auto;
  align-self: center;
  font-weight: 700; }
  .rightBar-collapserIcon .icon {
    font-size: 1.2rem; }

.appLayout-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }

.appLayout-header {
  flex-shrink: 0; }
  .theme-light .appLayout-header {
    border-bottom: 2px solid; }
  .theme-dark .appLayout-header {
    color: rgba(244, 244, 244, 0.7);
    background-color: #303846;
    border-bottom: 2px solid #1b2028; }
  @media print {
    .appLayout-header {
      display: none; } }

.appLayout-content {
  overflow-y: auto;
  flex-grow: 1; }
  @media print {
    .appLayout-content {
      overflow-y: visible; } }

.appLayout-footer {
  flex-shrink: 0; }
  .theme-light .appLayout-footer {
    border-top: 1px solid; }
  .theme-dark .appLayout-footer {
    color: rgba(244, 244, 244, 0.7);
    background-color: #303846;
    border-top: 1px solid black; }
  @media print {
    .appLayout-footer {
      display: none; } }
