html {
  /* switching to border-box model for all elements */
  box-sizing: border-box;
  /* set base font-size to equiv "10px", which is adapted to rem unit */
  font-size: 62.5%;
  /* IE9-IE11 math fixing. See http://bit.ly/1g4X0bX */
  font-size: calc(1em * 0.625); }

body {
  font-size: 1.6rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  line-height: 1.5; }

.cmLine {
  margin: 0;
  font-family: "PT Mono", consolas, courier, monospace;
  white-space: pre-wrap; }
  .editorPreview .cmLine {
    padding: 0 1rem;
    font-size: 1.2rem; }
  .exportPreview .cmLine {
    font-size: 1.2rem; }
  .cmLine .playRenderer {
    padding: 0 1rem; }
  .playRenderer.cmLine--fontSize-5 .cmLine {
    font-size: 7px; }
  .playRenderer.cmLine--fontSize-4 .cmLine {
    font-size: 8px; }
  .playRenderer.cmLine--fontSize-3 .cmLine {
    font-size: 9px; }
  .playRenderer.cmLine--fontSize-2 .cmLine {
    font-size: 10px; }
  .playRenderer.cmLine--fontSize-1 .cmLine {
    font-size: 11px; }
  .playRenderer.cmLine--fontSize0 .cmLine {
    font-size: 12px; }
  .playRenderer.cmLine--fontSize1 .cmLine {
    font-size: 13px; }
  .playRenderer.cmLine--fontSize2 .cmLine {
    font-size: 14px; }
  .playRenderer.cmLine--fontSize3 .cmLine {
    font-size: 15px; }
  .playRenderer.cmLine--fontSize4 .cmLine {
    font-size: 16px; }
  .playRenderer.cmLine--fontSize5 .cmLine {
    font-size: 17px; }
  .printPreview .cmLine {
    color: black; }
  .cmLine .playRenderer {
    padding: 0 1rem; }

.theme-dark .editorPreview .cmTextLine {
  color: #dbdbdb; }

.editorPreview .cmLine--isFromSectionRepeat .cmTextLine {
  opacity: 0.5; }

.theme-dark .playRenderer .cmTextLine {
  color: #dbdbdb; }

.theme-dark .printPreview .cmTextLine {
  color: #303846; }

.editorPreview .cmChordSymbol {
  font-weight: 700; }
  .theme-dark .editorPreview .cmChordSymbol {
    color: #dbdbdb;
    background-color: #404c4e; }

.editorPreview .cmLine--isFromSectionRepeat .cmChordSymbol,
.editorPreview .cmLine--isFromAutoRepeatChords .cmChordSymbol,
.editorPreview .cmLine--isFromChordLineRepeater .cmChordSymbol {
  opacity: 0.3;
  background-color: transparent; }

.theme-dark .playRenderer.cmChordSymbol--chordsColor-base .cmChordSymbol {
  color: #dbdbdb; }

.theme-dark .playRenderer.cmChordSymbol--chordsColor-red .cmChordSymbol {
  color: #ff7060; }

.theme-dark .playRenderer.cmChordSymbol--chordsColor-yellow .cmChordSymbol {
  color: #ecab06; }

.theme-dark .playRenderer.cmChordSymbol--chordsColor-green .cmChordSymbol {
  color: #74f14e; }

.theme-dark .playRenderer.cmChordLine--highlightChords .cmChordSymbol {
  background-color: #30393a; }

.printPreview .cmChordSymbol {
  font-weight: 700; }

.theme-dark .printPreview.cmChordLine--highlightChords .cmChordSymbol {
  background-color: #e7e7e7; }

.exportPreview .cmChordSymbol {
  font-weight: normal; }

.cmBarSeparator {
  margin: 0 10px; }
  .cmBarSeparator:first-of-type {
    margin-left: 0; }
  .cmBarSeparator:last-of-type {
    margin-right: 0; }
  .theme-dark .editorPreview .cmBarSeparator {
    color: #a8a8a8; }
  .editorPreview .cmLine--isFromChordLineRepeater .cmBarSeparator,
  .editorPreview .cmLine--isFromSectionRepeat .cmBarSeparator {
    opacity: 0.3; }
  .theme-dark .playRenderer.cmChordSymbol--chordsColor-base .cmBarSeparator {
    color: #a8a8a8; }
  .theme-dark .playRenderer.cmChordSymbol--chordsColor-red .cmBarSeparator {
    color: #ff9e93; }
  .theme-dark .playRenderer.cmChordSymbol--chordsColor-yellow .cmBarSeparator {
    color: #bb8704; }
  .theme-dark .playRenderer.cmChordSymbol--chordsColor-green .cmBarSeparator {
    color: #99f57d; }
  .theme-dark .printPreview .cmBarSeparator {
    color: #a8a8a8; }

.editorPreview .cmSectionLabel,
.playRenderer .cmSectionLabel,
.printPreview .cmSectionLabel {
  display: inline-block;
  width: 100%;
  margin-bottom: 0.5rem;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 90% !important;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 2em;
  border-bottom-style: dotted;
  border-bottom-width: 1px; }

.theme-dark .editorPreview .cmSectionLabel {
  color: #dbdbdb;
  border-color: #a8a8a8; }

.editorPreview .cmLine--isFromSectionRepeat .cmSectionLabel {
  opacity: 0.3; }

.theme-dark .playRenderer .cmSectionLabel {
  color: #dbdbdb;
  border-color: rgba(244, 244, 244, 0.7); }

.exportPreview .cmSectionLabel {
  text-transform: uppercase; }

.editorPreview .cmTimeSignature,
.playRenderer .cmTimeSignature,
.printPreview .cmTimeSignature {
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 90%;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 2em; }

.theme-dark .editorPreview .cmTimeSignature {
  color: #dbdbdb; }

.theme-dark .playRenderer .cmTimeSignature {
  color: #dbdbdb; }
