.sb-optionsGroup {
  padding-top: 2rem; }

.sb-optionsGroup-title {
  padding: 0.5rem 1rem 0.5rem 0.5rem;
  display: flex;
  font-size: 1.4rem;
  font-weight: 700; }
  .theme-light .sb-optionsGroup-title {
    border-bottom: 2px solid; }
  .theme-dark .sb-optionsGroup-title {
    border-bottom: 2px solid rgba(244, 244, 244, 0.7); }
  .theme-dark .sb-optionsGroup-isNotInteractable .sb-optionsGroup-title {
    color: #71819e; }
  .sb-optionsGroup:not(.sb-optionsGroup-isNotInteractable) .sb-optionsGroup-title:hover {
    cursor: pointer; }
    .theme-dark .sb-optionsGroup:not(.sb-optionsGroup-isNotInteractable) .sb-optionsGroup-title:hover {
      background-color: #596883; }

.sb-optionsGroup-label {
  flex: 1 1 auto; }

.sb-optionsGroup-toggle {
  flex: 0 0 auto; }

.sb-optionsGroup-iconTitle .icon {
  font-size: 1.6rem; }

.sb-optionsGroup-iconToggle .icon {
  font-size: 1.8rem; }
