.playRendererWrapper1 {
  position: relative;
  height: 100%;
  overflow-y: scroll; }
  .theme-dark .playRendererWrapper1 {
    background-color: #121616; }

.playRendererWrapper2 {
  margin: 1rem;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.playRenderer--columns-1 {
  column-count: 1; }

.playRenderer--columns-2 {
  column-count: 2;
  column-fill: auto;
  height: 100%; }

.playRenderer--columns-3 {
  column-count: 3;
  column-fill: auto;
  height: 100%; }

.playRenderer--columns-4 {
  column-count: 4;
  column-fill: auto;
  height: 100%; }
