.printPreview {
  overflow-y: scroll; }
  .theme-dark .printPreview {
    background-color: #293132; }
  @media print {
    .printPreview {
      background-color: white !important;
      overflow-y: auto; } }

.printPreview-page {
  position: relative;
  margin: 2rem auto;
  background-color: white; }
  @media print {
    .printPreview-page {
      margin: 0;
      page-break-after: auto; } }

.printPreview-pageContentWrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

.printPreview-pageContent {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  height: 100%; }

.printPreview-pageHeader {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem; }

.printPreview-pageColumnWrapper {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; }

.printPreview-pageColumn {
  flex: 1 1 0;
  margin: 0 0.7rem; }
  .printPreview-pageColumn:first-of-type {
    margin-left: 0; }
  .printPreview-pageColumn:last-of-type {
    margin-right: 0; }

.printPreview-page--a4 {
  width: 210mm;
  height: 297mm; }

.printPreview-page--booxmax2pro {
  width: 698px;
  height: 930px; }

.printPreview-pageContentWrapper--padding1 {
  padding: 3mm; }

.printPreview-pageContentWrapper--padding2 {
  padding: 6mm; }

.printPreview-pageContentWrapper--padding3 {
  padding: 9mm; }

.printPreview-pageContentWrapper--padding4 {
  padding: 12mm; }

.printPreview-pageContentWrapper--padding5 {
  padding: 15mm; }

.printPreview-page--font-5 {
  font-size: 7px; }

.printPreview-page--font-4 {
  font-size: 8px; }

.printPreview-page--font-3 {
  font-size: 9px; }

.printPreview-page--font-2 {
  font-size: 10px; }

.printPreview-page--font-1 {
  font-size: 11px; }

.printPreview-page--font0 {
  font-size: 12px; }

.printPreview-page--font1 {
  font-size: 13px; }

.printPreview-page--font2 {
  font-size: 14px; }

.printPreview-page--font3 {
  font-size: 15px; }

.printPreview-page--font4 {
  font-size: 16px; }

.printPreview-page--font5 {
  font-size: 17px; }
