.logo-isExpanded {
  margin: 0.5rem 1rem;
  padding-bottom: 0.5rem;
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 700; }
  .theme-light .logo-isExpanded {
    border-bottom: 1px dotted; }
  .theme-dark .logo-isExpanded {
    color: #f4f4f4;
    border-bottom: 1px dotted rgba(244, 244, 244, 0.7); }
  .leftBar-isCollapsed .logo-isExpanded {
    display: none; }

.logo-icon {
  font-size: 1.4rem;
  margin-right: 0.5rem; }

.logo-isCollapsed {
  padding: 1rem 0; }
  .leftBar:not(.leftBar-isCollapsed) .logo-isCollapsed {
    display: none; }
